import React, { Component } from "react";
import {
  Container,
  Row,
  Col,
  Navbar,
  Form,
  Button,
  Jumbotron,
  Card,
  Spinner,
} from "react-bootstrap";

class App extends Component {
  state = {
    status: "initial",
    username: "",
    details: {},
  };

  handleOnClick() {
    this.setState({
      status: "loading",
    });
    const url = "https://api.github.com/users/" + this.state.username;
    console.log(url);
    fetch(url)
      .then((results) => {
        if (results.ok) return results.json();
        else throw new Error("User not found!");
      })
      .then((data) => {
        this.setState({
          name: data.name,
          status: "success",
          details: data,
        });
      });
  }
  myChangeHandler = (event) => {
    this.setState({ username: event.target.value });
  };

  render() {
    return (
      <Container>
        <Row>
          <Col>
            <Navbar>
              <Navbar.Brand>React Github</Navbar.Brand>
              <Navbar.Toggle />
            </Navbar>
          </Col>
        </Row>
        <Row>
          <Col md={{ span: 6, offset: 3 }}>
            <Navbar>
              <Form.Control
                type="text"
                onChange={this.myChangeHandler}
                placeholder="Enter the Github Id"
              />
              <Button
                style={{ marginLeft: "5px" }}
                variant="primary"
                onClick={() => {
                  this.handleOnClick();
                }}
              >
                Search
              </Button>{" "}
              <br />
            </Navbar>
          </Col>
        </Row>
        <br></br>
        {this.state.status === "initial" && (
          <Jumbotron>
            <p>Enter the Github User Id</p>
          </Jumbotron>
        )}
        {this.state.status === "success" && (
          <Row>
            <Col md={{ span: 6, offset: 4 }}>
              <Card style={{ width: "18rem" }}>
                <Card.Img variant="top" src={this.state.details.avatar_url} />
                <Card.Body>
                  <Card.Title>{this.state.details.name}</Card.Title>
                  <Card.Text>{this.state.details.bio}</Card.Text>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        )}
        {this.state.status === "loading" && (
          <Spinner animation="border" variant="primary" />
        )}
      </Container>
    );
  }
}

export default App;
